/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap');

.project-header {
    margin-left: 295px; /* Adjusted to match sidebar width */
}

/* Media query for smaller screens */
@media (max-width: 768px) {
  .project-header {
    margin-left: -30px; /* Adjusted to match sidebar width */
}
}

/* Styles for the thumbnail image */
.project-header img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    z-index: 0; /* Ensure thumbnails are always on top */
}

/* Media query for smaller screens */
@media (max-width: 768px) {
  .project-logo img {
    width: 100%;
    height: 100%;
}
}

/* Base styles for the page */
.project-showcase-page {
    font-family: 'Poppins', sans-serif;
    padding: 40px;
    position: relative;
}

/* Content area styles */
.content {
    margin-top: -20px;
    margin-left: 320px; /* Adjusted to match sidebar width */
    padding: 20px;
    z-index: 1;
}

/* Common styles for sections */
.section {
    margin-bottom: 40px;
}

/* Headings */
.section h1,
.section h2,
.section h3,
.section h4 {
    margin-bottom: 10px;
    font-weight: 700;
    color: #eedcee;
}

.section h1 {
    font-size: 2.5rem;
}

.section h2 {
    font-size: 2rem;
}

.section h3 {
    font-size: 1.5rem;
}

.section h4 {
    font-size: 1.2rem;
}

/* Lists */
.section ul,
.section ol {
    margin-bottom: 20px;
    padding-left: 20px;
}

.section ul li,
.section ol li {
    margin-bottom: 5px;
    line-height: 1.6;
}

/* Links */
.section a {
    color: #007bff; /* Blue */
    text-decoration: none;
}

.section a:hover {
    text-decoration: underline;
}

/* Horizontal rule */
.section hr {
    border: none;
    border-top: 1px solid #eee; /* Light gray */
    margin: 40px 0;
}

/* Sidebar styles */
.sidebar {
    width: 250px;
    position: fixed;
    height: 100%;
    background-color: rgb(20, 16, 21); /* Dark background color */
    overflow-y: auto;
    z-index: 2;
    top: 0;
    left: 0;
    font-size: 15px;
    padding-right: 20px;
    padding-left: 20px;
    border-right: 5px solid rgb(179,179,179); /* Add a right border for a modern look */
}

.sidebar h1, .sidebar h2, .sidebar h3 {
    margin-bottom: 20px;
    font-weight: 700;
}

.sidebar ul {
    padding-left: 20px; /* Add some padding to the left */
    color: rgb(168, 175, 179);
}

.sidebar li {
    margin-bottom: 10px; /* Add spacing between list items */
}

.sidebar a {
    color: rgb(168, 173, 177); /* Set the default text color */
    text-decoration: none; /* Remove underline */
    transition: color 0.5s, transform 0.5s; /* Add transition for color change and scaling */
}

/* Active link style */
.active {
    color: rgb(178, 193, 203) !important; /* Change color for active link */
    font-weight: bold;
}

/* Fading-in rectangle for active sidebar */
.sidebar.active {
    background-color: rgba(42, 42, 42, 0.8); /* Semi-transparent dark background */
    transition: background-color 0.5s; /* Add transition effect */
}

.showcase-image {
    width: 60%;
    height: auto;
    border-radius: 5px;
}

@media (max-width: 768px) {
    .showcase-image {
        width: 100%;
        height: auto;
        border-radius: 5px;
    }
  }

/* Sidebar Scrollbar */
.sidebar {
    overflow-y: auto; /* Ensure the sidebar can scroll vertically */
    scrollbar-width: 0.2px; /* For Firefox */
}

/* Webkit (Chrome, Safari, etc.) scrollbar styles */
.sidebar::-webkit-scrollbar {
    width: 8px; /* Width of the scrollbar */
}

.sidebar::-webkit-scrollbar-track {
    background: rgb(171, 144, 172); /* Track color */
}

.sidebar::-webkit-scrollbar-thumb {
    background: rgb(89, 67, 91); /* Thumb color */
    border-radius: 4px; /* Rounded corners */
}

.sidebar::-webkit-scrollbar-thumb:hover {
    background: rgb(92, 56, 90); /* Thumb color */
}

/* Regular Page Scrollbar */
body {
  scrollbar-width: thin; /* For Firefox */
}

/* Webkit (Chrome, Safari, etc.) scrollbar styles */
body::-webkit-scrollbar {
  width: 8px; /* Width of the scrollbar */
}

body::-webkit-scrollbar-track {
  background: rgb(224, 224, 224); /* Track color */
}

body::-webkit-scrollbar-thumb {
  background: rgb(179, 179, 179); /* Thumb color */
  border-radius: 4px; /* Rounded corners */
}

body::-webkit-scrollbar-thumb:hover {
  background: rgb(51, 51, 51); /* Thumb color on hover */
}

.toc-level-1 {
    list-style-type: none; /* Use bullets for list items */
    margin-left: -10px;
    font-size: 14px;
    font-weight: bold;
}

.toc-level-2 {
    list-style-type: disc; /* Use bullets for list items */
    margin-left: 10px;
    font-size: 14px;
}

.toc-level-3 {
    list-style-type: circle; /* Use bullets for list items */
    margin-left: 40px;
    font-size: 13px;
}

.toc-level-4 {
    list-style-type: circle; /* Use bullets for list items */
    margin-left: 70px;
    font-size: 12px;
}

/* Media query for smaller screens */
@media (max-width: 768px) {
    .sidebar {
        width: 125px; /* Halve the width of the sidebar */
        display: none;
    }
    .content {
        margin-left: -35px; /* Adjust the content margin accordingly */
    }
}
