/* Header.css */

.header {
  padding-left: 20px;
}

@media (min-width: 768px) {
  .header-content {
    display: flex;
    align-items: center; /* Align items vertically */
  }
}

.text-content {
  flex: 1; /* Take up remaining space */
  max-width: 1000px; /* Adjust this width as necessary */
}

.text-content h1 {
  height: 80px; /* Set a fixed height for the <h1> */
  overflow: hidden; /* Hide overflow content */
  align-items: center; /* Center text vertically */
}

.text-content p:first-of-type {
  margin-left: 26px; /* Add some space between the text and the image */
  text-indent: -0.75em; /* Pulls the first line back to align with the rest */
}

.text-content p:first-of-type::first-letter {
  font-size: 2em; /* Adjust the font size as needed */
  font-weight: bold; /* Optional: Makes the first letter bold */
  font-family: "Old English Text MT", serif; /* Font choice for the first letter */
}

@media (max-width: 768px) {
  .avatar-container {
    margin-left: 20px; /* Add some space between the text and the image */
    display: none;
  }

  /* Adjust the font size of the typing text for smaller screens */
  .typing-text {
    font-size: 0.5em; /* Smaller size for smaller screens, adjust as needed */
  }
}

.badge-group {
  display: flex;
  flex-wrap: wrap;
  gap: 4px; /* Adjust the gap between badges as needed */
}

.badge-group img {
  border-radius: 7px; /* Add rounded edges */
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.5); /* Add blurred semi-transparent black outline */
  transition: transform 0.3s ease; /* Add transition for transform property */
}

.badge-group img:hover {
  transform: translateY(-5px); /* Move the badge upward on hover */
}

.coding-with {
  transition: transform 0.5s ease;
  width: 80%;
  padding-left: 20px;
}

@import url('https://fonts.googleapis.com/css2?family=Syncopate&display=swap');

.cursor {
  font-family: 'Syncopate', sans-serif; /* Set font to Syncopate */
  font-weight: bold; /* Ensure bold weight */
  color: purple; /* Set color to purple */
}

.typing-text {
  display: inline-block;
}

.cursor {
  display: inline-block;
  animation: blink 1s step-end infinite;
}

/* Blinking cursor animation */
@keyframes blink {
  50% {
    opacity: 0;
  }
}
