
/* App.css */

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap');

.app {
  font-family: 'Montserrat', sans-serif; /* Change the font for the entire body */
  padding: 20px; /* Adjust the padding value as needed */
  position: relative;
  color: #eedcee; /* Set a light color for the text */
  margin: auto; /* Center the component horizontally */
  width: 100%; /* Fixed width */
  height: auto; /* Fixed width */
}

.background-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1; /* Ensure the colored background is behind the animated background */

}

.colored-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(22, 18, 28);
  z-index: -2; /* Ensure the colored background is behind the animated background */
}


.avatar {
  border-radius: 50%; /* This will make the image circular */
  margin-bottom: 20px;
}

body {
  background-color: rgb(22, 18, 28);
  margin: 0; /* Remove default margin */
  padding: 0; /* Remove default padding */
}

.footer {
  text-align: center; /* Center-align the content */
}

