.home {
  padding: 20px;
}

.project-content {
  display: grid;
  grid-gap: 40px;
  align-items: center;
}

.category-section {
  margin-bottom: 40px;
}

.category-title {
  background-image: radial-gradient(circle farthest-corner at center, rgba(180, 121, 168, 0.03) 50%, transparent 100%),
  radial-gradient(circle farthest-corner at left, rgba(180, 121, 168, 0.02) 50%, transparent 100%);
  padding-left: 40px;
  margin-bottom: 10px;
  font-size: 20px;
  margin-left: -20px;
  color: rgb(22,18,28);
}

.project-row {
  padding-left: 60px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr)); /* Adjust minmax value to fit card size */
  gap: 30px; /* Space between cards */
}

/* Ensure card size and layout is consistent */
.project-card {
  width: 100%; /* Adjust as needed for responsiveness */
  height: auto; /* Adjust height as needed */
}
