/* src/components/Illumination.css */

.illumination {
    position: absolute;
    width: 700px; /* Make the illumination larger */
    height: 700px; /* Make the illumination larger */
    background: radial-gradient(circle, rgba(255, 255, 255, 0.015) 0%, rgba(255, 255, 255, 0) 70%),
                radial-gradient(circle, rgba(255, 255, 255, 0.015) 0%, rgba(255, 255, 255, 0) 50%),
                radial-gradient(circle, rgba(255, 255, 255, 0.015) 0%, rgba(255, 255, 255, 0) 30%),
                radial-gradient(circle, rgba(255, 255, 255, 0.015) 0%, rgba(255, 255, 255, 0) 10%);
    pointer-events: none; 
    transform: translate(-50%, -50%);
    z-index: -1;
  }
  