/* ProjectCard.css */

/* Styles for the project card container */

.project {
  width: calc(95.00% - 20px); /* Set a fixed width for each card (adjust as needed) */
  height: auto; /* Allow the height to adjust based on content */
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.4s ease, background-color 0.4s ease; /* Add transition for background color change */
  position: relative;
  overflow: hidden;
  margin-left: -25px;
}

.project:hover {
  transform: translateY(-10px);
}

/* Dark overlay styles */
.project::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* Styles for the thumbnail image */
.project img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0; /* Ensure thumbnails are always on top */

}

.thumbnail {
  transition: filter 0.4s ease; /* Add transition for filter change */
  filter: brightness(0.4); /* Darken the image */
}

.thumbnail:hover {
  filter: brightness(0.3); /* Darken the image upon hovering */
}

/* Styles for text inside the project card */
.project h2,
.project h4,
.project p,
.project a {
  position: relative;
  z-index: 2; /* Ensure text is above thumbnails */
  color: #fff;
  background-image: radial-gradient(circle farthest-corner at center, rgba(0, 0, 0, 0.15) 50%, transparent 100%),
  radial-gradient(circle farthest-corner at left, rgba(0, 0, 0, 0.075) 50%, transparent 100%);

  text-shadow:
  -1.5px -1.5px 0 #000,  
  1.5px -1.5px 0 #000,
  -1.5px  1.5px 0 #000,
  1.5px  1.5px 0 #000,
  -1.5px  0   0 #000,
  1.5px  0   0 #000,
   0   -1.5px 0 #000,
   0    1.5px 0 #000; /* Outline color */
    }



.libraries {
  margin-bottom: 60px; /* Add margin at the bottom to create space for the links */
}
.library {
  display: inline-block;
  padding: 5px 10px;
  border-radius: 8px;
  color: #ffffff; /* Ensure text is white for better contrast */
  margin: 5px;
  font-size: 0.9em;
  text-align: center; /* Center text within the box */
  position: relative; /* Ensure z-index works */
  z-index: 1; /* Set z-index to 1 */
  font-size: 12px;
  text-transform: uppercase; /* Make text all caps */
  font-weight: bold; /* Make text bold */
}
/* Other styles remain unchanged */

/* Styles for link circles */
.links {
  position: absolute;
  bottom: 10px; /* Adjust as needed */
  left: 20px; /* Adjust as needed */
  z-index: 1; /* Ensure the links stay below other elements */
}

.link-circle {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #000;
  margin-right: 5px;
  transition: scale 0.4s ease; /* Add transition for filter change */
}

.link-circle:hover {
  background-color: #333;
  scale: 0.9;
}